@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*, div, .row{
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	
}

a{
	text-decoration: none;
	-moz-user-select: text;
}



/* ----header-------- */

.headerPosition {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
}

.headerBlock{
	margin: 0 auto;
	background-color: black;
	color: white;
	min-height: 50px;
	padding: 10px;
}


.socialIcons a{
	display: inline-block;
	height: 32px;
	margin: 0 1%;
}

.socialIcons a:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.rightSide{
	text-align: right;
}

.rightSide a{
	text-decoration: none;
	color: white;
	text-transform: uppercase;
	display: block;
	height: 40px;
	float: left;
}
.signIn, .language {
	height: 35px;
}

.language img{
	cursor: pointer;
}

.signIn img:hover, .language img:hover{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}



.signIn>a>span{
	line-height: 35px;
	vertical-align: top;
	font-size: 12px;
}


.language>select{
	height: 35px;
	line-height: 35px;
	vertical-align: top;
	font-size: 12px;
	border: transparent;
	outline: none;
	background-color: black;
	color: white;
	cursor: pointer;
}


.header{
	min-height: 40px;
	line-height: 40px;
	vertical-align: middle;
	background-color: #3f51b5;
	padding: 10px;
}

.logo{
	margin: 0 auto;
	text-align: center;
	
}

.logo:hover{
	opacity: 0.7;
}

.headerNavMenu a{
	display: block; 
	color: white;
	text-transform: uppercase;
	text-decoration: none;
}

.headerNavMenu>ul{
	margin: 0 auto;
	text-align: right;

}

.headerNavMenu>ul>li{
	display: inline-block;
	list-style: none;
	height: 40px;
	box-sizing: border-box;

}

.headerNavMenu>ul>li>a{
	padding: 0 20px;

}

.headerNavMenu>ul>li:hover {
	opacity: 0.7;
}

div.headerNavMenu>ul>li>a{
	background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAHCAYAAAAxrNxjAAAAS0lEQVQYlXWO2QkAIAxD4waO4v4/ruAmjvCkIuLRFgLNURoBGSiAApiXbalAD8KmmVdP8oYv3RW9Y+/Nif0h6vR1TjN9T1m0bVnSAGIaytngCT5rAAAAAElFTkSuQmCC);
	background-repeat: no-repeat;
	background-position:right;

}

div.headerNavMenu>ul>li>a:only-child{
	background-image: none;

}

.navButtons{
	margin: 0 auto;
	text-align: center;
}

.navButtons>div{
	display: inline-block;
	border-left: 1px solid grey;
	width: 40px;
	height: 30px;
	line-height: 30px;
	vertical-align: middle;
	margin: 0 auto;
}

.navButtons button{
	border: none;
	height: 30px;
	width: 39px;
	background: transparent;
	cursor: pointer;
}

.navButtons>div:first-child{
	border-left: none;
}

.navButtons>div:first-child button{
	width: 40px;
}

.navButtons>div:nth-child(1){
	background-image: url(data:image/png;base64,);
	background-position: center;
	background-repeat: no-repeat;
}
.navButtons>div:nth-child(2){
	background-image: url(data:image/png;base64,);
	background-position: center;
	background-repeat: no-repeat;
}
.navButtons>div:nth-child(3){
	background-image: url(data:image/png;base64,);
	background-position: center;
	background-repeat: no-repeat;
}

.navButtons>div:hover{
	opacity: 0.7;
}

.headerSlider{
	min-height: 650px;
	background-image: url(/NewsSiteReact/static/media/layer.fc52c6c6.png);
	background-size: cover;
	padding: 100px;
	font-family: 'Roboto', sans-serif;
}

.headerSlider div, .headerSlider div a{
	color:white;
	text-align: center;
}


.headerSlider h3,h1{
	text-transform: uppercase;
	margin: 15px;

}

.sliderButton{
	padding: 40px;
}

.sliderButton button{
	width: 170px;
	height: 47px;
	background-color: #303f9f;
	text-align: center;
	text-transform: uppercase;
	color: white;
	border: none;
	border-radius: 25px;
	cursor: pointer;
}

.sliderButton button:hover{
	color: rgba(255,255,255,0.7);
}

.authorPhoto{
	padding: 15px;
}
.authorPhoto img{
	margin: 0 auto;
}
.postData{
	color:grey;
	background-color: transparent;
	border: none;
	text-align: center;
}
.comentsLikes{
	padding: 30px;
}
.comentsLikes>div{
	display: inline-block;
	width: 150px;
	margin: 0 15px;
	color: grey;
}

/* ------main--------- */

.main{
	background-color: #f5f5f5;
	padding: 30px 1% 30px 5%;
}
.mainHeader{
	min-height: 60px;
	padding: 10px 20px;
	border-bottom: 1px solid #d1cdcd;
	background-color: white;
}
.sortBlog{
	text-transform: uppercase;
	height: 40px;
	line-height: 40px;
	vertical-align: middle;
	background-color: transparent;
	border:none;
	font-weight: bold;
	text-align: center;
	margin: 10px;
}

.sortBlog button{
	border: none;
	outline: none;
	cursor: pointer;
	text-align: center;
	background-color: #f5f5f5;
	border-radius: 25px;
	text-transform: uppercase;
	box-sizing: border-box;
	padding: 10px 15px;
	font-weight: bolder;
	
}




.sortBlog button:hover{
	border: 1px solid #5767be;
	background-color: white;
	box-sizing: border-box;
	color:#5767be;
}


 div.verticalNews>button.likeButton, div.horizontalNews>button.likeButton, div.fullSize>button.likeButton {
	position: absolute;
	top:10px;
	right: 15px;
	height: 25px;
	width: 25px;
	z-index: 99;
}



div.verticalNews>button.likeButton>div, div.horizontalNews>button.likeButton>div, div.fullSize>button.likeButton>div {
	font-size: 30px;
	color:red;
}

.fullPageNews>button.likeButton{
	position: absolute;
	top:140px;
	right: 70px;
	height: 25px;
	width: 25px;
	border:none;
	outline: none;
	cursor: pointer;
	background: none;
	
}

.fullPageNews>button.likeButton>div{
	font-size: 30px;
	color:red;
}


.fullSize{
	background-color: white;
	margin-bottom: 15px;
	position: relative;
	text-align: center;
}

.fullSize a{
	color:black;
}
.fullSize a img{
	-webkit-transition: .8s ease-out;
    transition: .8s ease-out;
}

.fullSize a:hover img{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

 .fullSize a:hover>h3{
	opacity: 0.7;
}


.fullSize>.authorVideo, .fullSize h3,.fullSize p{
	margin: 15px;
	padding: 10px;
}

.fullSizeImg{
	width: 100%;
	overflow: hidden;
}

.fullSizeImg img{
	width: 100%;

}

.NewsImgVertical{
	width: 100%;
	height: 245px;
	overflow: hidden;
}

.NewsImgVertical img{
	height: 100%;
	min-width: 100%;
}

.NewsImgHorizontal{
	width: 100%;
	height: 100%;
	overflow: hidden;
}

.NewsImgHorizontal img{
	height: 100%;
	width: 100%;
}

.fullSize>button, .horizontalNews button, .verticalNews button{
	position: absolute;
	right:15px;
	bottom: 15px;
	border: none;
	outline: none;
	background:none;
	cursor: pointer;
	
}

.authorVideo{
	border-top: 1px solid #d1cdcd;
	vertical-align: middle;
	padding-top: 15px;
	text-indent: 15px;
	min-height: 70px;
	position: relative;
}

.authorVideo a{
	color:black;
}

.authorVideo a:hover{
	opacity: 0.7;
}

.authorVideo img{
	float: left;
	
}



.horizontal a{
	color:black;
}
.horizontal a img{
	-webkit-transition: .8s ease-out;
    transition: .8s ease-out;
}

.horizontal a:hover img{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

 .horizontal a:hover h3{
	opacity: 0.7;
}

.horizontalNews{
	display: grid;
	grid-template-columns: 300px 1fr;
	background-color: white;
	min-height: 245px;
	margin-bottom: 15px;
	position: relative;
}




.videoDescription{
	padding: 15px;
	background-color: white;
	
}
 .videoDescription p{
	margin-bottom: 15px;
	
}

.videoDescriptionShortText{
	max-height: 200px;
	overflow: hidden;
	text-overflow: ellipsis;
	margin-bottom: 15px;
	line-height: 26px;
}


.vertical{
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
	grid-gap: 15px;
	

}

.vertical a{
	color:black;
}
.NewsImgVertical a img{
	-webkit-transition: .8s ease-out;
    transition: .8s ease-out;
}

.NewsImgVertical a:hover img{
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.vertical .videoDescription a:hover h3{
	opacity: 0.7;
}

.verticalNews{
	position: relative;
	
}

.showMoreButton {
	width: 190px;
	height: 70px;
	/* padding: 20px; */
	margin: 15px auto;
}

.showMoreButton button {
	width: 170px;
	height: 50px;
	background-color: #303f9f;
	text-align: center;
	text-transform: uppercase;
	color: white;
	border: none;
	border-radius: 25px;
	cursor: pointer;
}

.showMoreButton button:hover{
	color: rgba(255,255,255,0.7);
}

/* --------------------aside----------------- */

.rightSidebar{
	background-color: white;
	padding: 20px;
	text-align: left;
}

.search {
	position: relative;
	width: 100%;
	height: 40px;
	background-color: #f5f5f5;
	border: none;
	outline: none;
	border-radius: 30px;
	margin-bottom: 30px;
}

.search input{
	height: 40px;
	background:none;
	border: none;
	outline: none;
	padding: 15px;
}

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration { display: none; }

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration { display: none; }


.search button{
	border: none;
	outline: none;
	background:none;
	height: 35px;
	width: 35px;
	border-radius: 50%;
	background-color: #ff4081;
	background-image: url(data:image/png;base64,);
	background-position: center;
	background-repeat: no-repeat;
	position: absolute;
	top:2.5px;
	right: 5px;
	cursor: pointer;
	opacity: 1;

}

.search button:hover{
	opacity: 0.7;

}

.sideBarTitle {
	text-transform: uppercase;
	color:#3f51b5;
	border-bottom: 1px solid #3f51b5;
	margin-top: 20px;
}


.categories li{
	list-style: none;	
	height: 40px;
	line-height: 40px;
	vertical-align: bottom;
	border-bottom: 1px solid #f5f5f5;
}

.categories a{
	text-decoration: none;
	color:black;
}

.categories a:hover{
	color:blue;
}

.subscribeLinks{
	max-width: 200px;
	margin: 0 auto;
}

.subscribeLinks a{
	margin: 0 2px;
}

.tags a{
	margin: 0 2px;
}

.subscribe input{
	width: 100%;
	height: 45px;
	padding: 5px;
}

.subscribe p{
	color:#595959;
	margin: 15px 0;
	text-align: left;
}

.subscribe button{
	width: 100%;
	height: 45px;
	margin-top: 20px;
	background-color: #3f51b5;
	color: white;
	text-transform: uppercase;
	border: none;
	outline: none;
	cursor: pointer;

}

.subscribe button:hover{
	color: rgba(255,255,255,0.7);
}

.subscribe a{
	cursor: pointer;
}



.subscribe a:hover{
	opacity: 0.7;
}
 
p.orTitle{
	overflow: hidden;
	margin: 15px 0;
	text-align: center;
}

.orTitle:before, .orTitle:after {
	content: '';
	display: inline-block;
	vertical-align: middle;
	box-sizing: border-box;
	width: 100%;
  	height: 1px;
  	background: grey;
  	border: solid #FFF;
  	border-width: 0 10px;
}

.orTitle:before{
	margin-left: -100%;
}

.orTitle:after{
	margin-right: -100%;
}


.tabPost .sideBarTitle{
	margin: 45px 0 25px;
}


.sideBarBlocks > input , .sideBarBlocks > div { 
  display: none; 
}


.sideBarBlocks  label { 
  display: block;
  padding: 10px; 
  border: 1px solid #3f51b5; 
  min-height: 40px;
  text-align: center;
  margin-right: -6px;
  cursor: pointer; 
  position: relative; 
  color: #3f51b5;
  text-transform: uppercase;
  font-size: 12px;
}


.sideBarBlocks label:hover{
color: darkblue;

}

.sideBarBlocks input[type="radio"]:checked + label { 

  background-color: #3f51b5;
  color:white;
 
}

.sideBarBlocks  input:nth-of-type(1):checked ~ div:nth-of-type(1),
.sideBarBlocks  input:nth-of-type(2):checked ~ div:nth-of-type(2) { 
  display: block; 
  margin-top: -6px;
  
}

.tabPostBlocks{
	border-bottom: 1px solid #eeeeee;
	padding: 25px 0;
}

.tabPostBlocks a{
	color:black;
}

.tabPostBlocks a:hover{
	opacity: 0.7;
}


.tabPostBlocks img{
	float:left;
	margin-right: 15px;
}

.tabPostBlocks p{
	color:grey;
	font-size: 12px;
}

.tags  .sideBarTitle{
	margin-bottom: 20px;
}

.tags a{
	display: inline-block;
	padding: 5px 10px;
	margin-top: 5px;
	background-color: #eeeeee;
	border-radius: 5px;
	color:black;
	text-decoration: none;
}

.tags a:hover{
	opacity: 0.7;
}

/* ------------footer------------------------- */



.footerSubscribe{
	min-height: 200px;
	background-image: url(/NewsSiteReact/static/media/footer-background.65bd06f6.png);
	background-repeat: no-repeat;
	background-size: cover;
	color:white;
	padding: 50px 4%;
}

.footerSubscribe h2{
	font-size: 40px;
}

.footerContainerLists{
	min-height: 80px;
	line-height: 50px;
	vertical-align: middle;
}

.emailFooter{
	width: 240px;
	height: 40px;
	background-color: transparent;
	border-radius: 15px;
	border:1px solid #909090;
	outline: none;
	text-indent: 20px;
	color:white;
	margin:15px;
}


.buttonSubscribeFooter{
	width: 140px;
	height: 40px;
	border-radius: 15px;
	border:none;
	background-color: #303f9f;
	text-align: center;
	color:white;
	text-transform: uppercase;
	cursor: pointer;
	margin:15px;
}

.buttonSubscribeFooter:hover{
	color: rgba(255,255,255,0.7);
}







/*--------------------------------------*/


@media only screen and (max-width: 575px){

	.leftSide{
		max-width: 245px;
		margin: 0 auto;
		vertical-align: middle;

	}

	.signIn {
		text-align: left;
	}

	.language {
		text-align: right;
	}

	.headerNavMenu>ul{
		text-align: center;
	
	}

	.headerNavMenu>ul>li{
		display: inline-block;
		height: 30px;
		margin-bottom: 5px;
		margin: 0 auto;
}

.headerNavMenu>ul>li>a{
		display: block;
		height: 30px;
		line-height: 30px;
		padding: 0;
}


.horizontalNews{
	grid-template-columns: 1fr;

}



.footerSubscribe h2{
	font-size: 35px;
}

.footerContainerLists button{
	margin: 0 25%;
}

}

/* ----------------------------------- */

@media (max-width: 768px){

	.headerNavMenu>ul>li>a{
	padding: 0 10px 0 0;

}


}
